import React, { useState, useRef, useEffect, useCallback, useMemo, memo } from 'react';
import ErrorBoundary from '../common/ErrorBoundary';
import styled from 'styled-components';
import { motion, AnimatePresence } from 'framer-motion';
import { useFocusTrap } from '../../hooks/useFocusTrap';
import { useUser } from '../../context/UserContext';
import socketService from '../../services/socketService';

// Import optimized sprite images
import maleSprite from '../../assets/images/maleSprite-small.png';
import femaleSprite from '../../assets/images/femaleSprite-small.png';

const FloatingModal = styled(motion.div).attrs({
  role: 'dialog',
  'aria-modal': true,
  'aria-labelledby': 'chat-title'
})`
  position: fixed;
  right: ${props => props.theme.spacing.xl};
  bottom: ${props => props.theme.spacing.xl};
  width: 360px;
  background: ${props => props.theme.colors.secondary};
  border: 1px solid ${props => props.theme.colors.glass.border};
  border-radius: ${props => props.theme.borderRadius.xl};
  overflow: hidden;
  box-shadow: ${props => props.theme.shadows.lg};
  backdrop-filter: blur(20px);
  
  @media (max-width: 768px) {
    width: calc(100% - ${props => props.theme.spacing.xl} * 2);
    right: ${props => props.theme.spacing.lg};
    bottom: ${props => props.theme.spacing.lg};
    max-height: 80vh;
  }
`;

const ModalHeader = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: ${props => props.theme.spacing.lg};
  background: ${props => props.theme.colors.tertiary};
  border-bottom: 1px solid ${props => props.theme.colors.glass.border};
  
  h2 {
    color: ${props => props.theme.colors.text.primary};
    font-size: ${props => props.theme.typography.fontSize.xl};
    font-weight: ${props => props.theme.typography.fontWeight.semibold};
  }
`;

const CloseButton = styled(motion.button)`
  background: none;
  border: none;
  color: ${props => props.theme.colors.text.secondary};
  cursor: pointer;
  padding: ${props => props.theme.spacing.xs};
  
  &:hover {
    color: ${props => props.theme.colors.text.primary};
  }
`;

const ModalContent = styled.div`
  padding: ${props => props.theme.spacing.lg};
  max-height: 400px;
  
  @media (max-width: 768px) {
    max-height: calc(80vh - 120px); // Account for header and input
  }
  overflow-y: auto;
  
  &::-webkit-scrollbar {
    width: 6px;
  }
  
  &::-webkit-scrollbar-track {
    background: ${props => props.theme.colors.glass.primary};
  }
  
  &::-webkit-scrollbar-thumb {
    background: ${props => props.theme.colors.glass.border};
    border-radius: ${props => props.theme.borderRadius.full};
  }
`;

const MessageBase = styled.article`
  display: flex;
  gap: ${props => props.theme.spacing.md};
  margin-bottom: ${props => props.theme.spacing.md};
  
  .avatar {
    width: 32px;
    height: 32px;
    border-radius: ${props => props.theme.borderRadius.full};
    background: ${props => props.theme.colors.glass.secondary};
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .content {
    flex: 1;
    background: ${props => props.theme.colors.glass.secondary};
    padding: ${props => props.theme.spacing.md};
    border-radius: ${props => props.theme.borderRadius.lg};
    color: ${props => props.theme.colors.text.primary};
  }
  
  &.sent {
    flex-direction: row-reverse;
    
    .content {
      background: ${props => props.theme.colors.accent};
      color: ${props => props.theme.colors.primary};
    }
  }
`;

const Input = styled.input.attrs({
  'aria-label': 'Type your message'
})`
  width: 100%;
  background: ${props => props.theme.colors.tertiary};
  border: 1px solid ${props => props.theme.colors.glass.border};
  border-radius: ${props => props.theme.borderRadius.lg};
  padding: ${props => props.theme.spacing.md};
  color: ${props => props.theme.colors.text.primary};
  margin-top: ${props => props.theme.spacing.lg};
  
  &:focus {
    outline: none;
    border-color: ${props => props.theme.colors.text.accent};
    box-shadow: ${props => props.theme.shadows.accent};
  }
  
  &::placeholder {
    color: ${props => props.theme.colors.text.secondary};
  }
`;

const OnlineUsers = styled.div`
  display: flex;
  align-items: center;
  gap: ${props => props.theme.spacing.xs};
  font-size: ${props => props.theme.typography.fontSize.sm};
  color: ${props => props.theme.colors.text.secondary};
  margin-left: ${props => props.theme.spacing.md};
`;

const OnlineIndicator = styled.div`
  width: 8px;
  height: 8px;
  background: ${props => props.$connected ? props.theme.colors.success : props.theme.colors.error};
  border-radius: 50%;
`;

const modalVariants = {
  hidden: { opacity: 0, y: 20, scale: 0.95 },
  visible: { 
    opacity: 1, 
    y: 0, 
    scale: 1,
    transition: { type: "spring", stiffness: 300, damping: 30 }
  },
  exit: { opacity: 0, y: 20, scale: 0.95 }
};

const Message = memo(({ message, userName }) => (
  <MessageBase
    className={message.isUser ? 'sent' : ''}
    aria-label={`${message.isUser ? (userName || 'You') : 'AI Assistant'} said: ${message.text}`}
  >
    <div className="avatar">
      {message.isUser ? (
        message.userGender ? (
          <img
            src={message.userGender === 'female' ? femaleSprite : maleSprite}
            alt={`${message.userGender} trainer sprite`}
            style={{
              width: '24px',
              height: '24px',
              imageRendering: 'pixelated'
            }}
          />
        ) : 'ðŸ‘¤'
      ) : 'ðŸ¤–'}
    </div>
    <div className="content">
      {message.isUser && userName && (
        <div style={{
          fontSize: '0.75rem',
          color: 'rgba(255,255,255,0.7)',
          marginBottom: '0.25rem',
          fontWeight: 'bold'
        }}>
          {userName}
        </div>
      )}
      {message.text}
    </div>
  </MessageBase>
));

Message.displayName = 'Message';

const Chat = ({ isOpen, onClose }) => {
  const modalRef = useRef(null);
  useFocusTrap(modalRef, isOpen);
  const { user } = useUser();
  
  const [messages, setMessages] = useState([]);
  const [inputValue, setInputValue] = useState('');
  const [onlineCount, setOnlineCount] = useState(0);
  const [isConnected, setIsConnected] = useState(false);
  const [isTyping, setIsTyping] = useState(false);
  const [typingUsers, setTypingUsers] = useState(new Set());
  const [connectionError, setConnectionError] = useState(null);
  const messagesEndRef = useRef(null);
  const typingTimeoutRef = useRef(null);

  // Initialize socket connection when chat opens
  useEffect(() => {
    if (!isOpen || !user.hasProfile) {
      return;
    }

    let isComponentMounted = true;

    const setupChat = async () => {
      try {
        // Connect to socket server
        await socketService.connect();
        
        if (!isComponentMounted) return;

        // Set up event listeners
        const handleRecentMessages = (recentMessages) => {
          if (!isComponentMounted) return;
          setMessages(recentMessages.map(msg => ({
            id: msg._id || msg.id,
            text: msg.content,
            isUser: msg.userName === user.name,
            userName: msg.userName,
            userGender: msg.userGender,
            timestamp: new Date(msg.timestamp)
          })));
        };

        const handleNewMessage = (message) => {
          if (!isComponentMounted) return;
          setMessages(prev => [...prev, {
            id: message.id,
            text: message.content,
            isUser: message.userName === user.name,
            userName: message.userName,
            userGender: message.userGender,
            timestamp: new Date(message.timestamp)
          }]);
        };

        const handleUserCountUpdate = (count) => {
          if (!isComponentMounted) return;
          setOnlineCount(count);
        };

        const handleUserJoined = (data) => {
          if (!isComponentMounted) return;
          setMessages(prev => [...prev, {
            id: `join-${Date.now()}-${Math.random()}`,
            text: `${data.userName} joined the chat`,
            isUser: false,
            isSystemMessage: true,
            timestamp: new Date(data.timestamp)
          }]);
        };

        const handleUserLeft = (data) => {
          if (!isComponentMounted) return;
          setMessages(prev => [...prev, {
            id: `leave-${Date.now()}-${Math.random()}`,
            text: `${data.userName} left the chat`,
            isUser: false,
            isSystemMessage: true,
            timestamp: new Date(data.timestamp)
          }]);
        };

        const handleUserTyping = (data) => {
          if (!isComponentMounted || data.userName === user.name) return;
          if (data.isTyping) {
            setTypingUsers(prev => new Set([...prev, data.userName]));
          } else {
            setTypingUsers(prev => {
              const newSet = new Set(prev);
              newSet.delete(data.userName);
              return newSet;
            });
          }
        };

        const handleConnect = () => {
          if (!isComponentMounted) return;
          console.log('Connected to chat server');
          setIsConnected(true);
          setConnectionError(null);
          
          // Join chat room after connection is established
          socketService.joinChat({
            userName: user.name,
            userGender: user.gender,
            room: 'general'
          });
        };

        const handleDisconnect = () => {
          if (!isComponentMounted) return;
          console.log('Disconnected from chat server');
          setIsConnected(false);
        };

        const handleConnectionError = (error) => {
          if (!isComponentMounted) return;
          console.error('Connection error:', error);
          setConnectionError('Failed to connect to chat server');
          setIsConnected(false);
        };

        const handleError = (error) => {
          console.error('Chat error:', error);
          if (error.message === 'User not found') {
            // Re-join the chat if user is not found
            socketService.joinChat({
              userName: user.name,
              userGender: user.gender,
              room: 'general'
            });
          }
        };

        // Register event listeners
        socketService.on('connect', handleConnect);
        socketService.on('disconnect', handleDisconnect);
        socketService.on('connect_error', handleConnectionError);
        socketService.on('recent_messages', handleRecentMessages);
        socketService.on('new_message', handleNewMessage);
        socketService.on('user_count_update', handleUserCountUpdate);
        socketService.on('user_joined', handleUserJoined);
        socketService.on('user_left', handleUserLeft);
        socketService.on('user_typing', handleUserTyping);
        socketService.on('error', handleError);

        // If already connected, join immediately
        if (socketService.getConnectionStatus()) {
          handleConnect();
        }

      } catch (error) {
        console.error('Failed to setup chat:', error);
        if (isComponentMounted) {
          setConnectionError('Failed to initialize chat');
        }
      }
    };

    setupChat();

    // Set initial welcome message
    setMessages([{
      id: 'welcome',
      text: `Welcome to the AI Careers chat, ${user.name}! Connect with other attendees and discuss AI career opportunities.`,
      isUser: false,
      isSystemMessage: true,
      timestamp: new Date()
    }]);

    // Cleanup function
    return () => {
      isComponentMounted = false;
      if (typingTimeoutRef.current) {
        clearTimeout(typingTimeoutRef.current);
      }
    };
  }, [isOpen, user.hasProfile, user.name, user.gender]);

  // Handle chat close cleanup
  useEffect(() => {
    if (!isOpen) {
      socketService.removeAllListeners();
      socketService.disconnect();
      setMessages([]);
      setIsConnected(false);
      setOnlineCount(0);
      setTypingUsers(new Set());
      setConnectionError(null);
    }
  }, [isOpen]);

  const scrollToBottom = useCallback(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, []);

  useEffect(() => {
    scrollToBottom();
  }, [messages, scrollToBottom]);

  const handleSendMessage = useCallback(() => {
    if (inputValue.trim() === '' || !isConnected) return;

    // Send message through socket
    socketService.sendMessage(inputValue.trim());
    
    // Clear input and stop typing indicator
    setInputValue('');
    socketService.stopTyping();
    
    // Clear typing timeout
    if (typingTimeoutRef.current) {
      clearTimeout(typingTimeoutRef.current);
    }
  }, [inputValue, isConnected]);

  const handleInputChange = useCallback((e) => {
    setInputValue(e.target.value);
    
    // Handle typing indicators
    if (isConnected && e.target.value.length > 0) {
      if (!isTyping) {
        setIsTyping(true);
        socketService.startTyping();
      }
      
      // Reset typing timeout
      if (typingTimeoutRef.current) {
        clearTimeout(typingTimeoutRef.current);
      }
      
      typingTimeoutRef.current = setTimeout(() => {
        setIsTyping(false);
        socketService.stopTyping();
      }, 1000);
    } else if (isTyping) {
      setIsTyping(false);
      socketService.stopTyping();
      if (typingTimeoutRef.current) {
        clearTimeout(typingTimeoutRef.current);
      }
    }
  }, [isConnected, isTyping]);

  const handleKeyPress = useCallback((e) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSendMessage();
    }
  }, [handleSendMessage]);

  if (!isOpen) return null;

  return (
    <ErrorBoundary>
    <FloatingModal
      ref={modalRef}
      variants={modalVariants}
      initial="hidden"
      animate="visible"
      exit="exit"
    >
      <ModalHeader>
        <h2 id="chat-title">
          ðŸ’¬ {isConnected ? 'Live Chat' : 'Chat (Connecting...)'}
          <OnlineUsers>
            <OnlineIndicator $connected={isConnected} />
            {isConnected ? `${onlineCount} online` : 'Connecting...'}
          </OnlineUsers>
        </h2>
        <CloseButton
          onClick={onClose}
          whileHover={{ scale: 1.1 }}
          whileTap={{ scale: 0.9 }}
          aria-label="Close chat"
        >
          Ã—
        </CloseButton>
      </ModalHeader>

      <ModalContent role="log" aria-live="polite" aria-label="Chat messages">
        <AnimatePresence>
          {messages.map((message) => (
            <Message key={message.id} message={message} userName={message.userName} />
          ))}
        </AnimatePresence>
        <div ref={messagesEndRef} />
      </ModalContent>

      <div style={{ padding: '1rem' }}>
        {typingUsers.size > 0 && (
          <div style={{ 
            fontSize: '0.75rem', 
            color: '#666', 
            marginBottom: '0.5rem',
            fontStyle: 'italic' 
          }}>
            {Array.from(typingUsers).join(', ')} {typingUsers.size === 1 ? 'is' : 'are'} typing...
          </div>
        )}
        {connectionError && (
          <div style={{ 
            fontSize: '0.75rem', 
            color: '#ff6b6b', 
            marginBottom: '0.5rem' 
          }}>
            {connectionError}
          </div>
        )}
        <Input
          value={inputValue}
          onChange={handleInputChange}
          onKeyPress={handleKeyPress}
          placeholder={isConnected ? "Type your message..." : "Connecting to chat..."}
          disabled={!isConnected}
        />
      </div>
    </FloatingModal>
    </ErrorBoundary>
  );
};

export default Chat;