import io from 'socket.io-client';

// Determine if we're in production based on hostname
const isProduction = typeof window !== 'undefined' && 
  (window.location.hostname === 'leochlon.github.io' || 
   window.location.hostname.includes('github.io'));

// Default backend URL
const DEFAULT_SERVER_URL = isProduction 
  ? 'https://ai-careers-backend.fly.dev'
  : (process.env.REACT_APP_API_URL || 'http://localhost:5002');

class SocketService {
  constructor() {
    this.socket = null;
    this.isConnected = false;
    this.eventListeners = new Map();
    this.connectionPromise = null;
  }

  async connect(serverUrl = DEFAULT_SERVER_URL) {
    console.log('Socket connecting to:', serverUrl);
    // If already connected or connecting, return existing promise
    if (this.socket && this.isConnected) {
      return Promise.resolve(this.socket);
    }

    if (this.connectionPromise) {
      return this.connectionPromise;
    }

    // Clean up any existing connection
    if (this.socket) {
      this.disconnect();
    }

    this.connectionPromise = new Promise((resolve, reject) => {
      this.socket = io(serverUrl, {
        transports: ['websocket', 'polling'],
        upgrade: true,
        rememberUpgrade: true,
        timeout: 10000,
        forceNew: true
      });

      const onConnect = () => {
        console.log('Connected to chat server');
        this.isConnected = true;
        this.socket.off('connect_error', onError);
        resolve(this.socket);
      };

      const onError = (error) => {
        console.error('Connection error:', error);
        this.isConnected = false;
        this.socket.off('connect', onConnect);
        reject(error);
      };

      this.socket.once('connect', onConnect);
      this.socket.once('connect_error', onError);

      // Set up persistent event handlers
      this.socket.on('disconnect', (reason) => {
        console.log('Disconnected from chat server:', reason);
        this.isConnected = false;
        this.connectionPromise = null;
      });
    });

    try {
      await this.connectionPromise;
      this.connectionPromise = null;
      return this.socket;
    } catch (error) {
      this.connectionPromise = null;
      throw error;
    }
  }

  disconnect() {
    if (this.socket) {
      this.socket.disconnect();
      this.socket = null;
      this.isConnected = false;
      this.connectionPromise = null;
    }
  }

  // Join a chat room
  joinChat(userData) {
    if (this.socket && this.isConnected) {
      console.log('Joining chat with data:', userData);
      this.socket.emit('join_chat', userData);
    } else {
      console.warn('Cannot join chat: not connected');
    }
  }

  // Send a message
  sendMessage(content) {
    if (this.socket && this.isConnected) {
      console.log('Sending message:', content);
      this.socket.emit('send_message', { content });
    } else {
      console.warn('Cannot send message: not connected');
    }
  }

  // Typing indicators
  startTyping() {
    if (this.socket && this.isConnected) {
      this.socket.emit('typing_start');
    }
  }

  stopTyping() {
    if (this.socket && this.isConnected) {
      this.socket.emit('typing_stop');
    }
  }

  // Event listener management
  on(event, callback) {
    if (this.socket) {
      this.socket.on(event, callback);
      
      // Store reference for cleanup
      if (!this.eventListeners.has(event)) {
        this.eventListeners.set(event, []);
      }
      this.eventListeners.get(event).push(callback);
    }
  }

  off(event, callback) {
    if (this.socket) {
      this.socket.off(event, callback);
      
      // Remove from stored references
      if (this.eventListeners.has(event)) {
        const listeners = this.eventListeners.get(event);
        const index = listeners.indexOf(callback);
        if (index > -1) {
          listeners.splice(index, 1);
        }
      }
    }
  }

  // Cleanup all listeners
  removeAllListeners() {
    if (this.socket) {
      this.eventListeners.forEach((listeners, event) => {
        listeners.forEach(listener => this.socket.off(event, listener));
      });
      this.eventListeners.clear();
    }
  }

  // Get connection status
  getConnectionStatus() {
    return this.isConnected && this.socket?.connected;
  }
}

// Create singleton instance
const socketService = new SocketService();

export default socketService;
